.loader {
    position: relative;
    color: #333;
    text-indent: -9999em;
    margin: 0 auto;
    -webkit-transform: translateY(.9375rem);
    transform: translateY(.9375rem)
}

.loader,
.loader:after,
.loader:before {
    background: #333;
    -webkit-animation: loaderAnim 1s infinite ease-in-out;
    animation: loaderAnim 1s infinite ease-in-out;
    width: .375rem;
    height: 1.5rem
}

.loader {
    -webkit-animation-delay: -.16s;
    animation-delay: -.16s
}

.loader:after,
.loader:before {
    position: absolute;
    top: 0;
    content: ''
}

.loader:before {
    left: -.625rem;
    -webkit-animation-delay: -.32s;
    animation-delay: -.32s
}

.loader:after {
    left: .625rem
}

@-webkit-keyframes loaderAnim {
    0%,
    100%,
    80% {
        -webkit-box-shadow: 0 0;
        box-shadow: 0 0;
        height: 1.5rem
    }
    40% {
        -webkit-box-shadow: 0 -.625rem;
        box-shadow: 0 -.625rem;
        height: 1.875rem
    }
}

@keyframes loaderAnim {
    0%,
    100%,
    80% {
        -webkit-box-shadow: 0 0;
        box-shadow: 0 0;
        height: 1.5rem
    }
    40% {
        -webkit-box-shadow: 0 -.625rem;
        box-shadow: 0 -.625rem;
        height: 1.875rem
    }
}

