.g-title {
    position: relative;
    font-family: Roboto-Bold;
    background-color: #eee;
    height: 2.5rem;
    line-height: 2.5rem;
    color: #000;
    font-size: .8125rem;
    letter-spacing: .05rem;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    border-left: .1875rem solid #0eb313;
    padding-left: 1.875rem
}

span.done {
    position: absolute;
    display: inline-block;
    pointer-events: none;
    font-size: 1.25rem;
    right: .3125rem;
    top: 50%;
    margin-top: -1.375rem;
    color: #4c4c4c;
    display: none
}

input.dirty:not(.invalid):valid+span.done {
    display: block
}

input.dirty:not(:focus).invalid,
input.dirty:not(:focus):invalid {
    border: 1px solid #d62700;
    color: #d62700
}

input.dirty:not(:focus).invalid::-webkit-input-placeholder,
input.dirty:not(:focus):invalid::-webkit-input-placeholder {
    color: #fe0000
}

input.dirty:not(:focus).invalid::-moz-placeholder,
input.dirty:not(:focus):invalid::-moz-placeholder {
    color: #fe0000
}

input.dirty:not(:focus).invalid::-ms-input-placeholder,
input.dirty:not(:focus):invalid::-ms-input-placeholder {
    color: #fe0000
}

input.dirty:not(:focus).invalid::placeholder,
input.dirty:not(:focus):invalid::placeholder {
    color: #fe0000
}

header {
    position: fixed;
    left: 0;
    top: 0;
    background-color: #fff;
    padding: 0 1rem;
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
