.color-switcher {
    float: left;
    position: relative;
    color: #fff;
    height: 2.5rem;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
    z-index: 99;
    margin-top: 1.0625rem;
    margin-left: .625rem
}

.color-switcher .chbx {
    width: 1.6875rem;
    height: 1.25rem;
    line-height: 1.25rem;
    border-radius: 1.25rem;
    padding: 0 .1875rem;
    font-size: .6875rem;
    cursor: pointer
}

.color-switcher .chbx:after,
.color-switcher .chbx:before {
    content: '';
    display: inline-block;
    margin-top: .1875rem;
    width: .875rem;
    height: .875rem;
    border-radius: 50%;
    display: none
}

.color-switcher .chbx:not(.active) {
    background-color: #e2e2e2;
    text-align: right;
    padding-right: .625rem
}

.color-switcher .chbx:not(.active):before {
    float: left;
    background-color: #000;
    display: block
}

.color-switcher .chbx.active {
    background-color: #2b2b2b;
    padding-left: .625rem;
    font-family: ROboto-Medium
}

.color-switcher .chbx.active:after {
    float: right;
    display: block;
    background-color: #bfbfbf
}

main {
    padding: 0;
    min-height: calc(100vh - 10.8125rem)
}

main iframe {
    display: block
}

footer {
    margin-top: 0
}


